import React, { useState } from "react";
import { Grid, Modal, Switch, Card, Button, Text } from "platyplex_ui";
import { gradient, primaryColor, primaryTxtColor } from "../../../../config";
import { Table, Input, Select, Progress, TimePicker, Popover, Popconfirm } from "antd";
import { Message } from "../../../client/form";
import { moment } from "../../../../utils/time";
import { Client } from "../../../../store/reducer/clients";
import { useDispatch, useSelector } from "react-redux";
import { Permit } from "../../../../store/reducer/permit";
import { removePermit } from "../../../../store/actions/permits";

enum HistoryState {
  ACTIVE = "ACTIVE",
  UPCOMING = "UPCOMING",
  EXPIRED = "EXPIRED",
  CANCELLED = "CANCELED",
}

const History = ({ client }: { client?: Client }) => {
  const admin = !client;
  const [state, setter] = useState({
    type: HistoryState.ACTIVE,
    checked: false,
  });
  const { clients } = useSelector((state: any) => state.clients);
  const { permits, loading } = useSelector((state: any) => state.permits);
  const setState = (props: any) => setter({ ...state, ...props });
  const checked = () => setState({ checked: true });
  const dispatch = useDispatch();
  const openPermit = async (permit: any, checked: any) => {
    const modal = Modal({
      title: "",
      modal: (
        <Modall 
          permit={permit} 
          second_vaccine={clients[0]?.second_vaccine || 7}
          checked={checked} 
          close={() => modal.close()} 
        />
      ),
      hideCancel: true,
    });
    modal.open();
  };

  const additionalInfo = {
    vaccine: clients[0]?.vaccine,
    communication: clients[0]?.communication
  };

  return (
    <>
      <Switch
        style={{ padding: 0 }}
        onClick={(type: any) => setState({ type })}
        color={primaryTxtColor}
        border={"2px solid " + primaryColor}
        items={Object.keys(HistoryState)}
        active={state.type}
      />
      <br />
      {state.type === HistoryState.UPCOMING ? (
        <Table
          bordered
          dataSource={permits}
          columns={upcommingColumns(additionalInfo)}
        />
      ) : (
        <Table
          bordered
          loading={loading}
          dataSource={permits
            .filter((c: any) => (state.checked ? c.key !== "1" : true))}
          columns={
            columns((e: any) => openPermit(e, checked), additionalInfo, (p: Permit)=> dispatch(removePermit(p)) )}
        />
      )}
    </>
  );
};

const Modall = ({ close, checked, permit, second_vaccine }: any) => {
  const [done, setDone] = useState(false);
  const nextState = () => setDone(true);
  second_vaccine = Number(second_vaccine) || 7;

  return (
    <Grid height="100%" width="100%">
      <Card
        shadow
        grid
        placeItems="stretch"
        style={{ minWidth: "40vw", maxWidth: "95vw" }}
        customCols="auto 1fr"
        mobileLayout={false}
      >
        <i
          onClick={close}
          className="fa fa-times-circle txt-md pointer hoverr"
          style={{
            gridRow: done ? "span 1" : "span 7",
            padding: ".25em",
            placeSelf: "start",
            color: primaryColor,
          }}
        />
        {done ? (
          <Message
            generate
            user={{
              id: "CBL61W21",
              endDate: moment(permit.starts).add("days", second_vaccine).toISOString(),
            }}
            admin
            style={{ gridGap: "1em" }}
            confirm={() => {
              checked();
              close();
            }}
          />
        ) : (
          <>
            <Text bold style={{ marginTop: "1.5em" }}>
              Schedule Second Vaccination E-Permit
            </Text>
            <Text textAlign="left">
              E-Permit ID: <b>{permit.ePermit}</b>
            </Text>
            <Text textAlign="left">
              Next Date:{" "}
              <b>
                {moment(permit.starts)
                  .add("days", second_vaccine)
                  .format("ddd, MMM, DD, YYYY")}
              </b>
            </Text>
            <Text textAlign="left" color={primaryColor}>
              Select Time
            </Text>
            <TimePicker format="hh:mm a" minuteStep={15} />
            <Button.Normal
              style={{
                borderRadius: "1.3em",
                padding: ".5em",
                marginTop: "1em",
                backgroundImage: `${gradient}`,
                color: "white",
                border: 0,
                placeSelf: "start",
                minWidth: "15vw",
              }}
              onClick={() => {
                nextState();
              }}
              type="submit"
            >
              Schedule
            </Button.Normal>
          </>
        )}
      </Card>
    </Grid>
  );
};

const columns = (openBooking: any, additionalInfo: any, removePermit: (p : Permit)=> void) => [
  {
    title: () => (
      <Grid placeItems="center start">
        Vaccine Manufacturer
        <Select style={{ width: 65 }} defaultValue="All">
          <Select.Option value={"Moderna"}>Moderna</Select.Option>
          <Select.Option value={"Pfizer"}>Pfizer</Select.Option>
        </Select>
      </Grid>
    ),
    render: () => additionalInfo?.vaccine || "Moderna",
  },
  {
    title: () => (
      <Grid placeItems="center start">
        E-Permit ID
        <Input prefix={<i className="fa fa-search" />} style={{ width: 65 }} />
      </Grid>
    ),
    dataIndex: "ePermit",
  },
  {
    title: () => (
      <Grid placeItems="center start">
        First Name
        <Input prefix={<i className="fa fa-search" />} style={{ width: 65 }} />
      </Grid>
    ),
    dataIndex: "firstName",
  },
  {
    title: () => (
      <Grid placeItems="center start">
        Last Name
        <Input prefix={<i className="fa fa-search" />} style={{ width: 65 }} />
      </Grid>
    ),
    dataIndex: "lastName",
  },
  {
    title: () => (
      <Grid placeItems="center start">
        Email
        <Input prefix={<i className="fa fa-search" />} style={{ width: 80 }} />
      </Grid>
    ),
    dataIndex: "email",
  },

  {
    title: () => (
      <Grid placeItems="center start">
        Phone Number
        <Input prefix={<i className="fa fa-search" />} style={{ width: 80 }} />
      </Grid>
    ),
    render: (i: any) => i.phone || "623-143-9124",
  },

  {
    title: () => (
      <Grid placeItems="center start">
        ParkStash Space
        <Select style={{ width: 60 }} defaultValue="All" />
      </Grid>
    ),
    render: (s: any)=> 'Space #' + (s.key || '1'),
  },
  {
    title: () => (
      <Grid placeItems="center start">
        License Plate #
        <Input prefix={<i className="fa fa-search" />} style={{ width: 65 }} />
      </Grid>
    ),
    dataIndex: "liscensePlate",
  },
  {
    title: () => (
      <Grid placeItems="center start">
        Appointment Date
        <Input prefix={<i className="fa fa-search" />} style={{ width: 100 }} />
      </Grid>
    ),
    dataIndex: "starts",
  },

  {
    title: () => <Grid placeItems="center start">Help Requested</Grid>,
    render: () => (
      <Text>
        {additionalInfo?.communication?
        <i className={"fa fa-check-circle"} style={{ color: "green" }} />:
        <i className={"fa fa-times-circle"} style={{ color: "red" }} />}
      </Text>
    ),
  },

  {
    title: () => (
      <Grid placeItems="center start">
        Progress
        <Input prefix={<i className="fa fa-search" />} style={{ width: 100 }} />
      </Grid>
    ),
    render: (_: any) => (
      <Progress percent={100 - (Number(_.key) - 1 || 0) * 10} />
    ),
  },

  {
    title: "Actions",
    render: (i: Permit) => (
      <>
        <Button rounded onClick={() => openBooking(i)} disabled={i.key !== "1"}>
          Schedule
        </Button>
        <Popconfirm
          title='Are you sure you want to cancel the permit?'
         onConfirm={() => removePermit(i)}
        >
        <Button rounded  bg='red'>
          Cancel
        </Button>
        </Popconfirm>  
      </>
    ),
  },
];

