import React, { useState } from "react";
import { Grid, Text, Header, Button } from "platyplex_ui";
import { useFormik } from "formik";
import { Input, DatePicker, Select } from "antd";
import { gradient, primaryColor } from "../../config";
import { moment } from "../../utils/time";
import { useDispatch, useSelector } from "react-redux";
import { addPermit } from "../../store/actions/permits";
import api from '../../api';
const defaultValue = {
  firstName: "",
  lastName: "",
  liscensePlate: "",
  email: "",
  starts: moment().set("minute", 0).format('ddd, MMM DD, YYYY @ hh:mm A'),
  phone: "",
  done: false,
  id: "2NE345678",
};

const Form = ({ admin }: any) => {
  const [user, setUser] = useState<any>(defaultValue);
  const submit = (values: any) => setUser(values);
  const reset = () => setUser(defaultValue);
  return user.done ? (
    <Message reset={reset} user={user} admin={admin} />
  ) : (
    <FormComp submit={submit} admin={admin} />
  );
};

export const Message = ({
  reset,
  user,
  admin,
  style,
  confirm,
  generate,
}: any) => {
  return (
    <Grid placeSelf="center" gridGap="2em" style={style}>
      <i
        className="fas fa-check-circle"
        style={{ color: primaryColor, fontSize: "6em" }}
      />
      <Header bold>E-permit reservation successful!</Header>
      <Text>
        Your E-permit Reservation ID: <b>{user?.id || "2NE3605"}</b>
      </Text>
      {!admin ? (
        <>
          <Text>
            Your E-permit reservation starts at{" "}
            <b>
              {user?.starts
                ? moment(user.starts).format(`ddd, MMM DD, YYYY @ hh:mm A`)
                : "Fri, Jan 29, 2021 @ 5:00 PM"}
              .
            </b>
          </Text>
          <Text>Confirmation details have been sent to your email.</Text>
        </>
      ) : generate ? (
        <Text>
          This E-Permit Expires:{" "}
          <b>
            {user?.endDate
              ? moment(user.endDate).format(`ddd, MMM DD, YYYY @ hh:mm A`)
              : "Fri, Dec 11, 2020 at 5:00 PM"}
            .
          </b>
        </Text>
      ) : (
        <Text>Confirmation details have been sent to patients email.</Text>
      )}
      <Button.Normal
        style={{
          borderRadius: "1.3em",
          padding: ".5em",
          marginTop: "1em",
          backgroundImage: `${gradient}`,
          color: "white",
          border: 0,
          minWidth: "15vw",
        }}
        type="submit"
        onClick={confirm ? confirm : reset}
      >
        Great
      </Button.Normal>
    </Grid>
  );
};
type Values =
  | "firstName"
  | "lastName"
  | "liscensePlate"
  | "email"
  | "starts"
  | "phone";

const vehicleFields = [
  {
    id: "firstName",
    label: "First Name",
    placeholder: "Ex: John",
  },
  {
    id: "lastName",
    label: "Last Name",
    placeholder: "Ex: Doe",
  },

  {
    id: "email",
    label: "Email Address",
    placeholder: "Ex: johndoe@gmail.com",
  },
  {
    id: "liscensePlate",
    label: "Your Vehicle License Plate #",
    placeholder: "Ex: 293DEF",
  },
  {
    id: "phone",
    label: "Your Phone number",
  },

  {
    id: "starts",
    label: "E-Permit Start Date & Time",
  },
] as any;

const FormComp = ({ submit, admin }: any) => {
  const dispatch = useDispatch();
  const { clients } = useSelector((state: any) => state.clients);
  const {
    handleSubmit,
    handleChange,
    errors,
    values,
    setFieldValue,
  } = useFormik({
    initialValues: defaultValue,
    async onSubmit(values) {
      dispatch(addPermit(values));

      api.sendEmail({
        firstName: values.firstName,
        lastName: values.lastName,
        email:  values.email,
        licensePlate: values.liscensePlate,
        phone: values.phone,
        vaccine: clients[0]?.vaccine,
        date: moment(values.starts).toISOString(),
        location: clients[0]?.name, 
        space: Number(clients[0]?.location.split('#')[1]) || 1,
      })
      .catch((e)=>console.log(e))
      submit({ ...values, done: true });
    },
  });

  const patients = clients[0]?.patients || 10;
  const error = vehicleFields.reduce(
    (acc: boolean, { id }: any) => acc || Boolean(errors[id as Values]),
    false
  );
  return (
    <Grid placeSelf="center" placeItems="start center" gridGap="1.5em">
      <Grid placeItems="stretch" placeSelf="stretch">
        {!admin ? (
          <>
            {" "}
            <Header textAlign="left" className="med">
              Hi there!
            </Header>
            <Text textAlign="left">
              Welcome to Sutter Health Vaccination E-permit reservation system.
            </Text>
            <Text textAlign="left">
              Please enter your information below to reserve your Vaccination
              E-permit.
            </Text>
          </>
        ) : (<>
          <Text bold textAlign="right" color={primaryColor}>({patients - 5}/{patients}) Appointments at {moment(values.starts).format('hh:mm a')}</Text>   
          <Text textAlign="left">
            Please enter patient information below to generate E-permit.
          </Text>
          </>
        )}
      </Grid>
      <form onSubmit={handleSubmit}>
        <Grid placeItems="stretch" cols="2" gridGap="2em 4em">
          {vehicleFields.map(
            ({
              id,
              label,
              placeholder,
            }: {
              id: Values;
              label: string;
              type: string | undefined;
              placeholder: string;
            }) => (
              <div style={{ paddingTop: ".5em" }} key={id}>
                {id === "liscensePlate" && admin ? (
                  <Text textAlign="left" color={primaryColor} bold>
                    Patient Vehicle License Plate #
                  </Text>
                ) : (
                  <Text textAlign="left" color={primaryColor} bold>
                    {label}
                  </Text>
                )}
                {id === "starts" ? (
                  <DatePicker
                    format="ddd, MMM, DD, YYYY, hh:mm a"
                    showTime={{ format: "hh:mm A" }}
                    minuteStep={15}
                    style={{ minWidth: "20vw", background: "transparent" }}
                    onChange={(e) => setFieldValue(id, e?.format('ddd, MMM DD, YYYY @ hh:mm A'))}
                    defaultValue={moment(values.starts)}
                  />
                ) : (
                  <Input
                    style={{ minWidth: "20vw", background: "transparent" }}
                    name={id}
                    value={values[id]}
                    onChange={handleChange}
                    placeholder={placeholder}
                  />
                )}
              </div>
            )
          )}
          <div style={{ paddingTop: ".5em" }}>
            <Text textAlign="left" color={primaryColor} bold>
              Select a Site
            </Text>

            <Select style={{width: '100%'}}>
              <Select.Option value={"site 1"}>Site 1</Select.Option>
            </Select>
          </div>
          <span />
          <Button.Normal
            style={{
              borderRadius: "1.3em",
              padding: ".5em",
              marginTop: "1em",
              ...(!error
                ? {
                    backgroundImage: `${gradient}`,
                    color: "white",
                    border: 0,
                  }
                : {}),
            }}
            type="submit"
            disabled={error}
            onSubmit={(e: any) => handleSubmit(e)}
          >
            Reserve E-Permit
          </Button.Normal>
        </Grid>
      </form>
    </Grid>
  );
};
export default Form;
