import React, { useState } from "react";
import { Grid, Button, Card, Text, Notification, List, Placeholder } from "platyplex_ui";
import Collapse from "../../../../components/collapse";
import {
  InputNumber,
  Switch as SwitchAtnd,
  Select,
  Popconfirm,
  Input,
  DatePicker,
} from "antd";
import Bar from "../../../../components/bar";
import { useDispatch, useSelector } from "react-redux";
import { Client } from "../../../../store/reducer/clients";
import { addClient, editClient, removeClient } from "../../../../store/actions/clients";
import { useFormik } from "formik";
import { NavLink } from "react-router-dom";

const Rules = () => {
  const { clients, loading } = useSelector((state: any) => state.clients);
  const [ add, setAdd ] = useState(false);

  const client= {
  id: "",
  name: "",
  open: false,
  parkingSpace: 0,
  overbooking: true,
  permits: 1, 
  duration: 0.1,
  location: 'Parking Lot 1',
  patients: 20,
    communication: true,
    vaccine: 'Moderna',
    second_vaccine: 7
};

  return (
    <>
    <Grid customCols='1fr auto' placeItems='stretch'>
      <Text bold textAlign="left" style={{ marginLeft: "1em" }}>
        Set/edit your Site E-permit rules below
      </Text>
      {!add && <Button rounded placeSelf='end' onClick={()=>setAdd(true)}>Add</Button>}
    </Grid>

      {add && <Card><ClientForm add client={client} cancelAdd={()=>setAdd(false)} /></Card>}
      <Card grid placeItems="stretch">
        <List 
        list={clients}
        loading={loading}
        loader={<>
        <Placeholder />
        <Placeholder />       
        </>}
          map={(client: Client, i : number) => (
            <React.Fragment key={i}>
              <Collapse
                heading={
                  <span>
                    {client.name} &nbsp;
                    <NavLink
                      target="_blank"
                      to={`/`}
                    >
                      <i className="fa fa-link" />
                    </NavLink>
                  </span>
                }
                collapse={!client.open || i === 0}
                pad="1em"
              >
                <ClientForm client={client} />
              </Collapse>
              <Bar />
            </React.Fragment>
          )}
        />
      </Card>
    </>
  );
};

const ClientForm = ({ client, cancelAdd, add }: { client: Client , cancelAdd? : ()=> void, add? : boolean}) => {
  const dispatch = useDispatch();
  
  const { handleSubmit, handleChange, values, setFieldValue } = useFormik({
    initialValues: client,
    async onSubmit(values) {
      dispatch(add? addClient(values): editClient(values));
      if(add && cancelAdd){
        cancelAdd()
      }
      Notification.success({
        title: "Success",
        message: "Changes Saved",
      });
    },
  });
  const format = 'ddd, MMM DD, YYYY';
  const error = !values.name;
  return (
    <form onChange={handleChange}>
      <Grid customCols="auto auto auto" placeSelf="stretch" placeItems="start">
        <Grid customCols="auto auto">
          Site Name
          <Input name={"name"} onChange={handleChange} value={values.name} />
        </Grid>
        <div>
          # of Parking Spaces &nbsp;{" "}
          <InputNumber
            value={values.parkingSpace}
            onChange={(e) => setFieldValue("parkingSpace", e)}
          />
        </div>

        <div>
          # of Patients Allowed &nbsp;{" "}
          <InputNumber
            value={values.patients}
            onChange={(e) => setFieldValue("patients", e)}
          />
        </div>

        <div>
          Default E-Permit Duration &nbsp;{" "}
          <Select
            value={values.duration}
            placeholder='10 mins'
            style={{ minWidth: 150 }}
            onChange={(e) => setFieldValue("duration", e)}
          >
             <Select.Option value={0.1}>10 mins</Select.Option>
            <Select.Option value={0.15}>15 mins</Select.Option>
            <Select.Option value={0.5}>30 mins</Select.Option>
            <Select.Option value={0.45}>45mins</Select.Option>
            <Select.Option value={0.45}>1 hour</Select.Option>
          </Select>
        </div>

        <div>
          Overstaying Allowed &nbsp;{" "}
          <SwitchAtnd
            checked={values.overbooking}
            onChange={(e: boolean) => setFieldValue("overbooking", e)}
          />
        </div>

        <div>
          Navigation Allowed &nbsp; <SwitchAtnd defaultChecked />
        </div>

        <div>
          Communication with patient allowed &nbsp;{" "}
          <SwitchAtnd
            checked={values.communication}
            onChange={(e: boolean) => setFieldValue("communication", e)}
          />
        </div>

        <div>
          Vaccine Manufacturer &nbsp;{" "}
          <Select
            value={values.vaccine}
            style={{ minWidth: 150 }}
            onChange={(e) => setFieldValue("vaccine", e)}
          >
             <Select.Option value={'Moderna'}>Moderna</Select.Option>
            <Select.Option value={'Pfizer'}>Pfizer</Select.Option>
          </Select>
        </div>
 
        <div>
          Second Vaccine Time &nbsp;{" "}
          <Select
            value={values.second_vaccine}
            style={{ minWidth: 150 }}
            onChange={(e) => setFieldValue("second_vaccine", e)}
          >
             <Select.Option value={7}>7 days</Select.Option>
            <Select.Option value={14}>14 days</Select.Option>
            <Select.Option value={21}>21 days</Select.Option>
          </Select>
        </div>
        <Grid customCols="auto auto">
          {cancelAdd?
          <Button rounded bg="red" onClick={cancelAdd}>
          Cancel
        </Button>:
          <Popconfirm
            title={`Are you sure you want to delete ${client.name}?`}
            onConfirm={() => dispatch(removeClient(client))}
          >
            <Button rounded bg="red">
              Remove
            </Button>
          </Popconfirm>}
          <Button disabled={error} rounded onClick={handleSubmit}>
            Save
          </Button>
        </Grid>
      </Grid>
    </form>
  );
};

export default Rules;
